import { useEffect, useState } from "react";
import { globalHistory } from "@reach/router";

const anchorIds = [];

function getAllIdOccurrences(toc) {
    toc.map(item => {
        if(item.items) {
            getAllIdOccurrences(item.items);
        }
        anchorIds.push(item.url.replace('#', ''));
    });
    return anchorIds;
}

export default function useTableOfContents(queryData: any): any {
    const [tableOfContents, setTableOfContents] = useState([]);
    const [anchorIds, setAnchorIds] = useState([]);
    useEffect(() => {
        // console.log('useTableOfContens', queryData);
        const tableOfContents = queryData.allMdx.nodes.find(node => node.frontmatter.path === globalHistory.location.pathname).tableOfContents.items[0].items;
        // console.log('useTableOfContens', tableOfContents);
        if (tableOfContents) {
            setTableOfContents(tableOfContents);
            setAnchorIds(getAllIdOccurrences(tableOfContents));
        }
        // console.log('useTableOfContens anchor', getAllIdOccurrences(tableOfContents));
    }, [queryData]);
    return [tableOfContents, anchorIds];
}